import appAttr from '../../components/page-component/app-attr/app-attr.vue';

export default {
    name: "u-waterfall",
    props: {
        value: {
            type: Array,
            required: true,
            default: function() {
                return [];
            }
        },
        addTime: {
            type: [Number, String],
            default: 200
        },
        idKey: {
            type: String,
            default: 'id'
        },
        theme: Object
    },
    provide() {
        return {
            uWaterfall: this
        }
    },
    data() {
        return {
            leftList: [],
            rightList: [],
            tempList: [],
            children: [],
            attrShow: 0,
            goods: null
        }
    },
    watch: {
        copyFlowList(nVal, oVal) {
            let startIndex = Array.isArray(oVal) && oVal.length > 0 ? oVal.length : 0;
            this.tempList = this.tempList.concat(this.cloneData(nVal.slice(startIndex)));
            this.splitData();
        }
    },
    mounted() {
        this.tempList = this.cloneData(this.copyFlowList);
        this.splitData();
    },
    computed: {
        copyFlowList() {
            return this.cloneData(this.value);
        }
    },
    methods: {
        async splitData() {
            if (!this.tempList.length) return;
            let leftRect = await this.uGetRect('#u-left-column');
            let rightRect = await this.uGetRect('#u-right-column');
            let item = this.tempList[0];
            if(!item) return ;
            if (leftRect && leftRect.height < rightRect && rightRect.height) {
                this.leftList.push(item);
            } else if (leftRect && leftRect.height > rightRect && rightRect.height) {
                this.rightList.push(item);
            } else {
                if (this.leftList.length <= this.rightList.length) {
                    this.leftList.push(item);
                } else {
                    this.rightList.push(item);
                }
            }
            this.tempList.splice(0, 1);
            if (this.tempList.length) {
                setTimeout(() => {
                    this.splitData();
                }, this.addTime);
            }
        },
        cloneData(data) {
            return JSON.parse(JSON.stringify(data));
        },

        uGetRect(element) {
            return new Promise((resolve) => {
                const query = uni.createSelectorQuery().in(this);
                query.select(element).boundingClientRect(data => {
                    resolve(data);
                }).exec();
            })
        },

        buy(item) {
            this.goods = item;
            this.attrShow = Math.random();
        },

        route(item) {
            uni.navigateTo({
                url: item.page_url
            });
        },
        emptyList() {
            this.leftList = [];
            this.tempList = [];
            this.rightList = [];
        }
    },

    components: {
        appAttr
    }
}
